import React, { useRef, useState } from 'react'
import { useDrop } from 'react-dnd'
import styled from "styled-components"

import RiderVideo from "../images/rider.mp4"

import ItemTypes from './item-types'
import ElectronicBicycle, { DefaultPosition as ElectronicBicycleDefaultPosition } from './ElectronicBicycle'
import Captain, { DefaultPosition as CaptainDefaultPosition } from './Captain'
import Jacket, { DefaultPosition as JacketDefaultPosition } from './Jacket'
import Junyeop, { DefaultPosition as JunyeopDefaultPosition } from './Junyeop'
import Lamy, { DefaultPosition as LamyDefaultPosition } from './Lamy'
import Pushups, { DefaultPosition as PushupsDefaultPosition } from './Pushups'
import ToteBag, { DefaultPosition as ToteBagDefaultPosition } from './ToteBag'

const styles = {
  width: "100%",
  height: "100%",
  border: '1px solid black',
  position: 'relative',
}

const BOX_TYPES = {
  ELECTRONIC_BICYCLE: 'ELECTRONIC_BICYCLE',
  CAPTAIN: 'CAPTAIN',
  JACKET: 'JACKET',
  JUNYEOP: 'JUNYEOP',
  LAMY: 'LAMY',
  PUSHUPS: 'PUSHUPS',
  TOTEBAGS: 'TOTEBAGS',
}


const Video = styled.video`
  display: ${props => props.show ? 'block' : 'none'};
  position: fixed;
  top: 50px;
  left: 500px;
  width: 450px;
  height: auto;
`;


const Container = ({ className = null, hideSourceOnDrag }) => {
  const videoRef = useRef(null);
  const [showVideo, setShowVideo] = useState(false)
  const [bicycles, setBicycles] = useState([{ ...ElectronicBicycleDefaultPosition }])
  const [captains, setCaptains] = useState([{ ...CaptainDefaultPosition }, { ...CaptainDefaultPosition }])
  const [lamies, setLamies] = useState([{ ...LamyDefaultPosition }])
  const [jackets, setJackets] = useState([{ ...JacketDefaultPosition }, {
    ...JacketDefaultPosition
  }])
  const [junyeops, setJunyeops] = useState([{
    ...JunyeopDefaultPosition
  }])
  const [pushups, setPushups] = useState([{ ...PushupsDefaultPosition }, { ...PushupsDefaultPosition }])
  const [totebags, setTotebags] = useState([{ ...ToteBagDefaultPosition }, {
    ...ToteBagDefaultPosition
  }])

  const setBoxes = (id, position = { left: 0, top: 0 }) => {
    const boxType = id.split('-')[0]
    const boxIndex = +id.split('-')[1]

    switch (boxType) {
      case BOX_TYPES.ELECTRONIC_BICYCLE: {
        const boxes = [...bicycles]
        boxes[boxIndex] = position
        setBicycles(boxes)
        break;
      }
      case BOX_TYPES.CAPTAIN: {
        const boxes = [...captains]
        if (boxes.length - 1 === boxIndex) {
          boxes.push({ ...CaptainDefaultPosition })
        }
        boxes[boxIndex] = position
        setCaptains(boxes)
        break;
      }
      case BOX_TYPES.JACKET: {
        const boxes = [...jackets]
        if (boxes.length - 1 === boxIndex) {
          boxes.push({ ...JacketDefaultPosition })
        }
        boxes[boxIndex] = position
        setJackets(boxes)
        break;
      }
      case BOX_TYPES.PUSHUPS: {
        const boxes = [...pushups]
        if (boxes.length - 1 === boxIndex) {
          boxes.push({ ...PushupsDefaultPosition })
        }
        boxes[boxIndex] = position
        setPushups(boxes)
        break;
      }
      case BOX_TYPES.TOTEBAGS: {
        const boxes = [...totebags]
        if (boxes.length - 1 === boxIndex) {
          boxes.push({ ...ToteBagDefaultPosition })
        }
        boxes[boxIndex] = position
        setTotebags(boxes)
        break;
      }
      case BOX_TYPES.JUNYEOP: {
        const boxes = [...junyeops]
        boxes[boxIndex] = position
        setJunyeops(boxes)
        break;
      }
      case BOX_TYPES.LAMY: {
        const boxes = [...lamies]
        boxes[boxIndex] = position
        setLamies(boxes)
        break;
      }
      default:
        break;
    }
  }

  const [, drop] = useDrop({
    accept: ItemTypes.BOX,
    drop(item, monitor) {
      const delta = monitor.getDifferenceFromInitialOffset()
      const left = Math.round(item.left + delta.x)
      const top = Math.round(item.top + delta.y)
      setBoxes(item.id, { left, top })
      return undefined
    },
  })

  const handleClickBicycle = () => {
    if (showVideo) {
      videoRef.current.pause();
    } else {
      videoRef.current.play();
    }
    setShowVideo(!showVideo)
  }

  return (
    <div className={className} ref={drop} style={styles}>

      {junyeops.map((j, index) => (
        <Junyeop position={j} key={index} id={`${BOX_TYPES.JUNYEOP}-${index}`} hideSourceOnDrag={hideSourceOnDrag} />
      ))}

      {jackets.map((j, index) => (
        <Jacket position={j} key={index} id={`${BOX_TYPES.JACKET}-${index}`} hideSourceOnDrag={hideSourceOnDrag} />
      ))}

      {pushups.map((p, index) => (
        <Pushups position={p} key={index} id={`${BOX_TYPES.PUSHUPS}-${index}`} hideSourceOnDrag={hideSourceOnDrag} />
      ))}

      {lamies.map((l, index) => (
        <Lamy position={l} key={index} id={`${BOX_TYPES.LAMY}-${index}`} hideSourceOnDrag={hideSourceOnDrag} />
      ))}

      {bicycles.map((b, index) => (
        <ElectronicBicycle onClick={handleClickBicycle} position={b} key={index} id={`${BOX_TYPES.ELECTRONIC_BICYCLE}-${index}`} hideSourceOnDrag={hideSourceOnDrag} />
      ))}

      {totebags.map((t, index) => (
        <ToteBag position={t} key={index} id={`${BOX_TYPES.TOTEBAGS}-${index}`} hideSourceOnDrag={hideSourceOnDrag} />
      ))}

      {captains.map((c, index) => (
        <Captain position={c} key={index} id={`${BOX_TYPES.CAPTAIN}-${index}`} hideSourceOnDrag={hideSourceOnDrag} />
      ))}

      <Video show={showVideo} src={RiderVideo} ref={videoRef} />
    </div>
  )
}

export default Container
