import React, { useCallback, useEffect, useRef, useState } from "react"
import styled from "styled-components"
import { DndProvider } from "react-dnd"
import Backend from "react-dnd-html5-backend"

import SEO from "../components/seo"
import Container from "../components/container"
import backgroundImg from "../images/momento-group.jpeg"
import { opened } from "../lib/momento"

const Self = styled.section`
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
`

const Background = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url(${backgroundImg});
  background-size: 1400px auto;
  z-index: 100;
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
`

const DndContainer = styled(Container)`
  z-index: 1030;
`

const Footer = styled.div``

const TimerWrapper = styled.div`
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  z-index: 1030;
  padding: 20px 0;
`

const TimerMessage = styled.div`
  font-family: "Helvetica Neue", "sans-serif" !important;
  font-style: italic;
  color: #072846;
  white-space: pre-wrap;
`

const TimerMessageWrapper = styled.div`
  display: inline-block;
`

const TimerTitle = styled.h1`
  font-family: "Helvetica Neue", "sans-serif" !important;
  font-style: italic;
  font-size: 295px;
  color: #072846;
  white-space: pre-wrap;
  margin: 0;
`

const IndexPage = () => {
  const [hideSourceOnDrag, setHideSourceOnDrag] = useState(true)
  const toggle = useCallback(() => setHideSourceOnDrag(!hideSourceOnDrag), [
    hideSourceOnDrag,
  ])
  const [open, setOpen] = useState(null)
  const timer = useRef(null)
  useEffect(() => {
    timer.current = window.setInterval(() => {
      if (opened()) {
        setOpen(true)
      } else {
        setOpen(false)
      }
    }, 100)
  }, [])

  return (
    <>
      <SEO title="Momento Brewers  " description="" />
      <DndProvider backend={Backend}>
        <Self>
          <Background />

          <TimerWrapper>
            <TimerMessageWrapper>
              <TimerMessage>
                Mon~Fri 08:00 ~ 17:00
              <br />
              Sat~Sun 11:00 ~ 19:00
            </TimerMessage>
            </TimerMessageWrapper>
          </TimerWrapper>

          <DndContainer hideSourceOnDrag={hideSourceOnDrag} />
        </Self>
      </DndProvider>
    </>
  )
}

export default IndexPage
