import React from "react"
import styled from "styled-components"

import Box from "./box"
import PushupsVideo from "../images/irene-pushups.mp4"

const Video = styled.video`
  width: 150px;
`

export const DefaultPosition = {
  top: 270,
  left: 665,
}

export default ({
  hideSourceOnDrag,
  id,
  position = {
    left: 0,
    top: 0,
  },
}) => {
  return (
    <Box
      key={id}
      id={id}
      left={position.left}
      top={position.top}
      hideSourceOnDrag={hideSourceOnDrag}
    >
      <Video src={PushupsVideo} autoPlay loop muted />
    </Box>
  )
}
