import React from "react"
import styled from "styled-components"

import Box from "./box"
import CaptainImg from "../images/captain.png"

const Image = styled.div`
  background-image: url(${CaptainImg});
  background-size: cover;
  width: 140px;
  height: 140px;
`

export const DefaultPosition = {
  top: 230,
  left: 165,
}

export default ({
  hideSourceOnDrag,
  id,
  position = {
    left: 0,
    top: 0,
  },
}) => {
  return (
    <Box
      key={id}
      id={id}
      left={position.left}
      top={position.top}
      hideSourceOnDrag={hideSourceOnDrag}
    >
      <Image src={CaptainImg} alt="" />
    </Box>
  )
}
